body {
  margin: 0;
  font-family: "Lato";
  font-weight: 900;
  font-size: 20px;
  color: #000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  user-select: none; /* supported by Chrome and Opera */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.googleSignInandoutbuttonsDiv {
  padding-top: 30px;
}

.storeFrontCart {
  color: #2c8a51;
}

.prompt {
  padding-left: 25px;
  font-size: 15px;
  font-weight: 200 !important;
}

.homeBanner {
  width: 300px;
  height: 200px;
  overflow-x: hidden;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.mainmenu {
  position: relative;
}

.menuDiv {
  background-color: #ffffff !important;
}

.mainmenu menuitem {
  display: inline;
}

.storeCardImage {
  border: 1px solid #06451f;
  border-radius: 50%;
  padding: 2px;
  width: 50px;
  height: 50px;
}

#root {
  overflow-x: hidden;
  width: 100%;
}

#bannerDiv {
  display: flex;
  justify-content: center;
  overflow-x: hidden;
  background-color: #ffffff;
  padding-top: 0px;
  padding-bottom: 0px;
}

#bannerDivShop {
  display: flex;
  justify-content: center;
}

#bannerDivMain {
  background-color: #ffffff;
  border-radius: 50px;
  padding: 7px;
  margin-bottom: 10px;
}

.TermsDiv {
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 12px;
  padding: 30px;
}

.termsAndConditionsLink span {
  color: #06451f !important;
  font-size: 8px important;
}

.frontstoreCard {
  margin-left: 60px;
  margin-top: 36px;
}

.frontstoreCard .content .frontPageStoreNameAndLocation {
  display: inline;
  margin-left: 20px;
  position: absolute;
  color: #06451f;
}

.frontstoreCard .content .description {
  display: inline;
  width: 64px;
  height: 64px;
}

.frontPageStoreCardImage {
  border: 1px solid #06451f;
  border-radius: 50%;
  padding: 3px;
  width: 60px;
  height: 60px;
}

.buyfromLocalShopsPhraseDiv {
  position: relative;
  margin-left: 12%;
  margin-top: 20px;
}
.buyfromLocalShopsPhraseDiv Span {
  color: #2c8a51;
  font-size: 14px;
}

.storeCard {
  width: 100%;
  display: flex;
  justify-content: center;
  overflow-x: hidden;
  margin-top: 10px;
}

.storeCard .ui.card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 5px 0 rgba(0, 0, 0, 0.19);
  margin: 3px 3px 3px 3px;
}

.switchMenuDiv {
  width: 100%;
  display: flex;
  justify-content: center;
  overflow-x: hidden;
  margin-top: 17px;
}

.Odo {
  color: red;
}

.loveDiv {
  display: flex;
  justify-content: center;
  overflow-x: hidden;
  margin-top: 17px;
  margin-bottom: 17px;
}

.loveDiv span {
  color: #6e2424 !important;
  font-size: 13px;
}

.switchMenuDivInner {
  color: #2c8a51;
}

.storePage {
  width: 100%;
  display: flex;
  justify-content: center;
  overflow-x: hidden;
  margin-top: 17px;
}

.shoppageheaderDiv {
  width: 100%;
  display: flex;
  justify-content: center;
  overflow-x: hidden;
  padding: 10px;
  margin-top: 35px;
  margin-bottom: 10px;
}

.storePageImage {
  border: 1px solid #2c8a51;
  border-radius: 50%;
  padding: 3px;
  width: 65px;
  height: 65px;
}

.storeName {
  width: 100%;
  display: flex;
  justify-content: center;
  overflow-x: hidden;
  overflow-y: hidden;
  margin-top: 10px;
  padding: 5px;
  color: #06451f;
  font-size: 20px;
  font-weight: 300;
}

.searchBar {
  width: 100%;
  display: flex;
  justify-content: center;
  overflow-x: hidden;
  margin-top: 17px;
}

.searchBar input {
  width: 300px;
  /*box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);*/
  margin: 3px 3px 3px 3px;
  height: 30px;
}

.categories {
  width: 100%;
  display: flex;
  justify-content: center;
  overflow-x: hidden;
  margin-top: 7px;
}

.categories .ui.dropdown.categories {
  color: #06451f;
}

.products {
  width: 100%;
  background-color: #ffffff;
  margin-top: 20px;
}

.productCard {
  width: 100%;
  display: flex;
  justify-content: center;
  overflow-x: hidden;
  margin-top: 17px;
  padding-top: 10px;
  padding-bottom: 10px;
  min-height: 220px;
}

.productCard img {
  position: absolute;
  width: 90px;
  height: 90px;
  margin-bottom: 20px !important;
  right: 25px;
}

.productCard i.icon.plus.circle:before {
  color: #f2711c;
}

.modalCartItems i.icon.plus.circle:before {
  color: #f2711c;
}
.modalCartItems i.icon.trash:before {
  color: #06451f;
}

.productCard span {
  color: #f2711c;
}

.productImage {
  position: absolute;

  width: 90px;
  height: 90px;
  border-radius: 15px;
}

.productQuantity {
  position: absolute;
  float: left;

  background-color: #06451f;
  width: 30px;
  height: 30px;
  padding-bottom: 4px;
  text-align: center;
  border-radius: 100%;
}
.productName {
  position: absolute;
  font-size: 15px;
  font-weight: bold;
  max-width: 55%;
  font-family: "Lato";
  bottom: 35px;
}

.productPrice {
  position: absolute;
  bottom: 5px;
  right: 7px;
}

.fourteen.wide.column {
  padding-top: 0px !important;
  margin-top: 0px !important;
}

.productQuantity span {
  color: #ffffff;
  font-weight: bold;
  font-size: 25px;
}

.orderTotalCart {
  margin-top: 10px;
  font-size: 15px;
  font-family: "Lato";
  font-weight: bold;
}

.productNameCart {
  text-transform: capitalize;
}

.orderTotalDivider {
  margin-top: 35px;
  color: #06451f;
}

.accountpageForm {
  width: 100%;
  display: flex;
  justify-content: center;
  overflow-x: hidden;
  margin-top: 17px;
}

.accountpageForm input {
  width: 300px !important;
}

.accountpageForm label {
  color: #ffffff !important;
  font-weight: 500 !important;
}

.needHelp {
  width: 100%;
  display: flex;
  justify-content: center;
  overflow-x: hidden;
  margin-top: 10px;
  color: #06451f;
}

.needHelp i {
  color: #0971f1;
  width: 10px;
}

#bannerText {
  width: 100%;
  display: flex;
  justify-content: center;
  overflow-x: hidden;
  margin-top: 5px;
  margin-bottom: 1px;
  color: #ffffff;
}

.storeFrontCart {
  color: #06451f;
}

#cartQuantity {
  position: absolute;
  color: #ff2b06;
  font-weight: bold;
  right: px;
  top: 10px;
}

#shoppingCartWrap {
  background: #ffffff !important;

  padding: 7px !important;
  border-radius: 30px !important;
  color: #ffffff !important;
}

#accountdetails label {
  color: #000 !important;
}
#accountdetails ::placeholder {
  color: #000 !important;
}

.googleSignInandoutbuttonsDiv {
  width: 100% !important;
  display: flex !important;
  justify-content: center !important;
  overflow-x: hidden !important;
}

.checkOutPageMainDiv {
  display: flex;
  flex-direction: row;
  font-size: 14px;
  justify-content: center;
}
.mainmenu {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.menuColumnWrap {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.menuLeftColumn {
  display: flex;
  flex-direction: row;
}

.menuRightColumn {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 7%;
}

.addorRemoveProductButton {
  position: absolute;
  bottom: 5px;
}

.supportDiv {
  margin-top: 55px;
  width: 100%;
  display: flex;
  justify-content: center;
  overflow-x: hidden;
}

.whatsappsupportDiv {
  background-color: #06451f;
  padding: 35px !important;
  border-radius: 15px;
  color: white;
}

.bannerCaption {
  position: absolute !important;
  margin-top: 10px !important;
  margin-left: -70px !important;
  color: #000;
  font-weight: 900;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

@media only screen and (min-width: 600px) {
  .frontPageBottomRow {
    display: flex !important;
    flex-direction: column !important;
    justify-content: center !important;
    padding-left: 180px;
  }

  .bannerCaption {
    position: absolute !important;
    margin-top: 10px !important;
    margin-left: -30% !important;
    color: #000;
    font-weight: 900;
    font-size: 60px;
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  }

  /**.renderStorelist {
    display: flex !important;
    flex-direction: column !important;
    justify-content: flex-start !important;
  }**/

  .buyfromLocalShopsPhraseDiv {
  }
  /* For Desktop: */

  .menuDiv {
    display: flex;
    justify-content: center;
    width: 100%;
    padding-right: 22%;
    padding-left: 22%;
  }

  .mainmenu {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
  }

  .buyfromLocalShopsPhraseDiv {
    display: flex;
    justify-content: center !important;
    margin-right: 30%;
  }

  .menuColumnWrap {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  .menuLeftColumn {
    display: flex;
    flex-direction: row;
  }

  .menuRightColumn {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 3% !important;
  }

  .bannerCaption {
    position: absolute !important;
    margin-top: 10px !important;
    margin-left: -45% !important;
    color: #000;
    font-weight: 900;
    font-size: 60px;
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  }

  .homeBanner {
    width: 700px;
    height: 400px;
    margin-left: 45%;
    overflow-x: hidden;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  }

  .searchBar input {
    width: 650px;
  }

  .storeCard {
    width: 100%;
    display: flex;
    justify-content: left;
    overflow-x: hidden;
    margin-top: 10px;
    margin-left: 25%;
  }

  .storeCard .ui.card {
    width: 500px !important;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  }

  .products {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
  }

  .categories .ui.form {
    width: 200px;
  }
  .productCard .ui.card {
    margin-left: 22%;
  }

  .productCard {
    width: 410px;
    min-height: 220px;
  }
  .productImage {
    position: absolute;
    margin-top: 30px;
  }

  .shoppageheaderDiv {
    margin-top: 35px;
  }
}
